html {
  font-family: monospace;
  height: 100%;
 }
 
 body,
 #root {
  margin: 0;
  height: 100%;
  width: 100%;
  color: white;
  background-color: #14213d;
 }

 header {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 1em;
  nav {
   width: 100%;
   ul {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 2em;
    gap: 3em;

    li {
     list-style: none;

     a {
      color: #fca311;
      text-decoration: none;
      font-size: 1.5em;
      font-weight: 500;
     }
    }
   }
  }
 }